body {
	font-family: "Proxima Nova", sans-serif;
	background-color: #fff;
}

hr {
	border-bottom: 3px solid #c4c4c4;
	width: 125px;
	margin: 0 auto;
	margin-bottom: 14px;

	@media screen and (min-width: 992px) {
		border-bottom: 5px solid #c4c4c4;
		margin-bottom: 30px;
		width: 150px;
	}
}

.button {
	display: inline-block;
	padding: 14px 40px;
	font-weight: 700;
	font-size: 15px;
	line-height: 15px;
	text-transform: uppercase;
	transition: all 0.3s ease-in-out;

	@media screen and (min-width: 992px) {
		padding: 16px 40px;
		font-size: 16px;
		line-height: 19px;
	}

	&-white {
		color: #040404;
		background-color: #fff;

		&:hover,
		&:active {
			color: #fff;
			background-color: #040404;
		}
	}

	&-black {
		color: #fff;
		background-color: #040404;
		border: 2px solid #111010;

		&:hover,
		&:active {
			color: #040404;
			background-color: #fff;
		}
	}

	&-big {
		padding: 14px 40px;

		@media screen and (min-width: 992px) {
			padding: 16px 54px;
		}
	}

	&-small {
		@media screen and (min-width: 992px) {
			padding: 18px 34px;
		}
	}
}

img {
	width: 100%;
	height: auto;
}

.container {
	padding: 0 20px;
	max-width: 1462px;
	margin: 0 auto;
}

.gradient {
	position: absolute;

	background-image: linear-gradient(
		0deg,
		rgba(17, 16, 16, 0) 0%,
		rgba(17, 16, 16, 0.0211887) 12.5%,
		rgba(17, 16, 16, 0.0211887) 88.02%,
		rgba(17, 16, 16, 0) 100%
	);
	transform: matrix(1, 0, 0, -1, 0, 0);

	background-repeat: no-repeat;
	background-size: cover;
	background-position-x: center;
	height: 509px;
}

.h2 {
	font-weight: 700;
	font-size: 30px;
	line-height: 48px;
	color: #111010;

	@media screen and (min-width: 992px) {
		font-size: 48px;
	}
}

.txt {
	color: #616161;
}

.txt-15 {
	font-size: 15px;
	line-height: 25px;

	@media screen and (min-width: 992px) {
		font-size: 20px;
		line-height: 28px;
	}
}

.txt-18 {
	color: #898989;
	font-weight: 600;
	font-size: 18px;
	line-height: 1;
}

.txt-20 {
	font-weight: 400;
	font-size: 20px;
	line-height: 28px;
}

.black {
	color: #111010;
}

.w-full {
	width: 100%;
}

.marg-b {
	margin-bottom: 60px;

	@media screen and (min-width: 992px) {
		margin-bottom: 120px;
	}
}

.d-flex {
	display: flex;
	justify-content: space-between;
	align-items: center;
	gap: 16px;

	@media screen and (min-width: 992px) {
		width: 100%;
	}
}

.df-64 {
	display: flex;
	gap: 64px;
}

.flex-column {
	display: flex;
	flex-direction: column;
}

.fl-start {
	align-items: flex-start;
}

.stretch {
	align-items: stretch;
}

.jcc {
	justify-content: center;
}

.aic {
	align-items: center;
}

.jc-around {
	justify-content: space-around;
}

.jc-between {
	justify-content: space-between;
}

.shrink-0 {
	flex-shrink: 0;
}

.gap-0 {
	gap: 0;
}

.relative {
	position: relative;
}

.header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding-top: 16px;
	padding-bottom: 16px;
	@media screen and (min-width: 992px) {
		flex-wrap: wrap;
		row-gap: 40px;
		padding-top: 44px;
		padding-bottom: 32px;
	}

	&-logo {
		width: 101px;
		height: 36px;

		@media screen and (min-width: 992px) {
			width: 167px;
			height: 57px;
			flex-basis: 100%;
			order: -1;
		}
	}

	&-right-nav {
		display: flex;
		align-items: center;
		justify-content: center;
		gap: 24px;
	}

	&-icon {
		width: 24px;
		height: 24px;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-shrink: 0;
	}

	nav {
		display: none;
		@media screen and (min-width: 992px) {
			display: flex;
			max-width: 890px;
			width: 100%;
			justify-content: space-between;
		}
	}

	&-link {
		font-weight: 700;
		font-size: 16px;
		line-height: 1.5;
		text-transform: uppercase;
		text-decoration: none;
		white-space: nowrap;
		color: #0a0a0a;
		transition: all 0.3s ease-in-out;

		&:hover {
			color: #979797;
		}

		&.active {
			color: #979797;
		}
	}
}

.hero-bg {
	background-image: url(../img/mob-main-bg.jpg);
	background-repeat: no-repeat;
	background-size: cover;
	height: 434px;

	@media screen and (min-width: 429px) {
		background-image: url(../img/main-bg.jpg);
		height: 526px;
	}

	@media screen and (min-width: 992px) {
		margin-bottom: 75px;
	}
}

.hero {
	background-color: #666a6f;
	padding: 30px 20px;
	margin-top: -30px;
	max-width: 716px;
	margin-bottom: 40px;

	@media screen and (min-width: 992px) {
		background: rgba(0, 0, 0, 0.36);
		backdrop-filter: blur(17px);
		padding: 50px 36px 52px 43px;
		margin: 0;
		position: absolute;
		top: 62px;
		right: 11.5%;
	}

	&-head {
		font-weight: 700;
		font-size: 26px;
		line-height: 26px;
		color: #ffffff;
		margin-bottom: 20px;

		@media screen and (min-width: 992px) {
			font-size: 48px;
			line-height: 48px;
			margin-bottom: 30px;
		}
	}

	&-parag {
		font-weight: 400;
		font-size: 18px;
		line-height: 28px;
		color: #ececec;
		margin-bottom: 13px;

		@media screen and (min-width: 992px) {
			font-size: 20px;
			line-height: 28px;
			margin-bottom: 60px;
		}
	}

	&-link {
		font-weight: 700;
		font-size: 15px;
		line-height: 15px;
		text-transform: uppercase;
		color: #040404;
		background-color: #fff;
		transition: all 0.3s ease-in-out;

		&:hover,
		&:active {
			color: #fff;
			background-color: #040404;
		}
	}
}

.products {
	margin-bottom: 25px;

	@media screen and (min-width: 992px) {
		margin-bottom: 40px;
	}

	.h2 {
		text-align: center;

		@media screen and (min-width: 992px) {
			margin-bottom: 20px;
		}
	}

	.txt {
		text-align: center;
		margin: 0 auto;
		margin-bottom: 26px;
		max-width: 635px;

		@media screen and (min-width: 992px) {
			margin-bottom: 30px;
		}
	}
}

.description {
	background-color: #000;
	border: 1px solid #ffffff;
	padding: 10px 35px;
	width: 93%;
	min-height: 93px;
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	flex-grow: 1;
	margin: 0 auto;
	transform: translate(0px, -50px);

	@media screen and (min-width: 370px) {
		padding: 20px 35px;
	}

	@media screen and (min-width: 992px) {
		position: unset;
		height: 114px;
		width: 93%;
		margin: 0 auto;
		margin-top: -85px;
		transform: translate(0px, -10px);
		flex-wrap: nowrap;
	}

	@media screen and (min-width: 992px) and (max-width: 1048px) {
		padding: 20px 15px;
	}

	&-header {
		font-weight: 600;
		font-size: 20px;
		line-height: 20px;
		text-align: center;
		color: #ffffff;

		@media screen and (min-width: 410px) {
			font-size: 28px;
			line-height: 28px;
		}

		@media screen and (min-width: 992px) {
			margin-bottom: 10px;
		}
	}

	&-parag {
		font-weight: 400;
		font-size: 12px;
		line-height: 13px;
		text-align: center;
		color: #b4b4b4;

		@media screen and (min-width: 410px) {
			font-size: 15px;
			line-height: 16px;
		}
	}
}

.swiper {
	width: calc(100% + 40px);
	height: auto;
	margin: 0 -20px !important;
	padding: 0 20px !important;

	&-button-next,
	&-button-prev {
		background-color: #fff;
		color: #000 !important;
		width: 37px !important;
		height: 37px !important;
		top: 40% !important;

		&::after {
			font-size: 18px !important;
		}
	}

	&-picture {
		object-fit: cover;
		aspect-ratio: 1/0.9;

		@media screen and (min-width: 992px) {
			width: 100%;
			height: auto;
			object-fit: unset;
			aspect-ratio: unset;
		}
	}

	&-pagination-bullets {
		bottom: 25px !important;
	}

	&-pagination-bullet {
		width: 5px !important;
		height: 5px !important;
		border-radius: 0 !important;
		background: #c4c4c4 !important;
		opacity: 1 !important;

		&-active {
			background: #000 !important;
		}
	}

	.swiper-slide {
		height: auto;
		display: flex;
		flex-direction: column;
	}
}

.swiper.simple {
	.swiper-button-next,
	.swiper-button-prev {
		top: 50% !important;
	}

	.swiper-picture {
		aspect-ratio: 1/0.839;
	}
}

.swiper.project {
	.swiper-button-next,
	.swiper-button-prev {
		top: 38% !important;
	}
}

.vision-bg {
	background-image: url(../img/vision.jpg);
	background-repeat: no-repeat;
	background-size: cover;
	background-position-x: center;
	height: 579px;
	margin-bottom: 60px;

	@media screen and (min-width: 992px) {
		background: none;
		height: 0;
		margin-bottom: 0;
	}
}

.vision {
	padding: 22px 20px 35px;
	background-color: #f7f7f7;
	max-width: 708px;
	margin: 0 auto -125px;
	z-index: 1;

	@media screen and (min-width: 992px) {
		padding: 50px 55px 52px 43px;
		margin-bottom: 0px;
		position: absolute;
		top: 12%;
	}

	.h2 {
		margin-bottom: 10px;

		@media screen and (min-width: 992px) {
			margin-bottom: 30px;
		}
	}

	.txt {
		margin-bottom: 24px;

		@media screen and (min-width: 992px) {
			margin-bottom: 60px;
		}
	}

	&-img {
		margin-bottom: 120px;
		display: block;
		width: 94.3%;
		height: auto;
		transform: translateX(6.5%);
	}
}

.solutions {
	flex-direction: column;
	row-gap: 0;
	max-height: 726px;
	flex-wrap: wrap;

	&-title {
		max-width: 610px;
		text-align: center;
		margin: 0 auto 17px;

		@media screen and (min-width: 992px) {
			margin: 0;
			text-align: left;
		}

		.h2 {
			line-height: 30px;
			margin-bottom: 20px;

			@media screen and (min-width: 992px) {
				margin-bottom: 30px;
				line-height: 58px;
			}
		}
	}
}

.video-block {
	position: relative;
	&-content {
		background-image: url(../img/video-butt.svg);
		background-repeat: no-repeat;
		background-position: 30px 25px;
		position: absolute;
		right: 0;
		bottom: 0;
		background-color: #fff;
		padding: 34px 30px 34px 110px;

		.txt-18:first-of-type {
			margin-bottom: 6px;
		}
	}
}

.projects {
	.h2 {
		text-align: center;

		@media screen and (min-width: 992px) {
			margin-bottom: 20px;
		}
	}

	.txt {
		text-align: center;
		margin: 0 auto;
		margin-bottom: 30px;
		max-width: 450px;

		@media screen and (min-width: 992px) {
			margin-bottom: 40px;
			max-width: 826px;
		}
	}
}

.project-cards {
	flex-wrap: wrap;
	justify-content: space-evenly;

	@media screen and (min-width: 1400px) {
		gap: 30px;
		justify-content: space-between;
		flex-wrap: nowrap;
	}
}

.title {
	margin-top: 23px;
	margin-bottom: 35px;

	@media screen and (min-width: 992px) {
		margin-top: 34px;
		margin-bottom: 50px;
	}

	&-head {
		font-weight: 600;
		font-size: 22px;
		line-height: 22px;
		text-align: center;
		text-transform: capitalize;
		color: #111010;
		margin-bottom: 4px;

		@media screen and (min-width: 350px) {
			font-size: 26px;
			line-height: 26px;
		}
	}

	&-parag {
		font-weight: 600;
		font-size: 16px;
		line-height: 26px;
		text-align: center;
		text-transform: capitalize;
		color: rgba(17, 16, 16, 0.5);
	}
}

.collections {
	.h2 {
		text-align: center;
		margin-bottom: 20px;
	}

	.txt {
		text-align: center;
		margin: 0 auto;
		margin-bottom: 40px;
		max-width: 826px;
	}

	&-cards {
		margin-bottom: 34px;
		display: grid;
		grid-template-columns: 1;
		gap: 30px;

		@media screen and (min-width: 768px) {
			grid-template-columns: repeat(2, 1fr);
		}

		@media screen and (min-width: 1120px) {
			grid-template-columns: repeat(3, 1fr);
		}

		@media screen and (min-width: 1485px) {
			grid-template-columns: repeat(4, 1fr);
		}
	}
}

.collections-card {
	position: relative;
	height: 470px;
	overflow: hidden;
	background-color: #f6f6f6;
	box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.04);
	transition: all 0.3s ease-in-out;

	&:hover,
	&:active {
		.collections-card-description {
			bottom: 0;

			.button {
				opacity: 1;
			}
		}
	}

	&-img {
		margin: 12px 0 16px;
	}

	&-price {
		margin-top: 15px;

		s {
			font-size: 12px;
			line-height: 16px;
			text-transform: capitalize;
			color: #cacaca;
		}

		span {
			font-weight: 600;
			font-size: 18px;
			line-height: 18px;
			text-transform: capitalize;
			color: rgba(17, 16, 16, 0.5);
		}
	}

	&-description {
		position: absolute;
		bottom: 0px;
		width: 100%;
		padding: 25px 16px 20px;
		background-color: #fff;
		transition: all 0.3s ease-in-out;
		align-items: center;

		@media screen and (min-width: 992px) {
			bottom: -73px;
		}

		h3 {
			font-weight: 600;
			font-size: 26px;
			line-height: 1;
			text-align: center;
			text-transform: capitalize;
			color: #111010;

			&:hover,
			&:active {
				text-decoration: underline;
			}
		}

		.button {
			opacity: 1;
			border: 2px solid #111010;
			font-size: 16px;
			line-height: 19px;
			margin-top: 20px;
			width: auto;
			padding: 16px 37px;
			transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out,
				opacity 0.8s ease-in-out;

			@media screen and (min-width: 992px) {
				opacity: 0;
			}
		}

		.d-flex {
			gap: 10px;
		}
	}
}

.showrooms-bg {
	background-image: url(../img/showrooms.jpg);
	background-repeat: no-repeat;
	background-size: cover;
	background-position-x: center;
	height: 669px;

	@media screen and (min-width: 1300px) {
		background: none;
		height: 0;
	}
}

.showrooms {
	display: flex;
	justify-content: center;
	@media screen and (min-width: 1300px) {
		margin-bottom: 120px;
	}

	&-img {
		display: none;
		@media screen and (min-width: 1300px) {
			display: block;
		}
	}

	&-info {
		position: relative;
		max-width: 692px;
		align-self: center;
		margin-left: -86px;
		padding: 22px 20px 47px;
		background-color: #f9f9f9;

		@media screen and (max-width: 1300px) {
			margin-left: 0;
			margin-bottom: -70px;
		}

		@media screen and (min-width: 992px) {
			padding: 80px 152px 80px 86px;
		}

		@media screen and (min-width: 1400px) {
			padding: 120px 152px 120px 86px;
		}

		.h2 {
			margin-bottom: 30px;
			line-height: 56px;

			@media screen and (min-width: 992px) {
				font-size: 56px;
			}
		}

		.txt {
			margin-bottom: 60px;
			max-width: 454px;

			@media screen and (min-width: 992px) {
				max-width: max-content;
			}
		}
	}
}

footer {
	background-color: #111010;
}

.footer {
	padding: 29px 0 20px;

	@media screen and (min-width: 992px) {
		padding: 70px 0 27px;
	}

	&-wrap {
		@media screen and (min-width: 992px) {
			display: flex;
			justify-content: space-between;
		}
	}

	&-logo {
		display: block;
		width: 122px;
		height: 43px;
		margin: 0 auto 38px;

		@media screen and (min-width: 992px) {
			margin: 0;
			width: 162px;
			height: 57px;
		}
	}

	&-nav {
		border-bottom: 1px solid #474747;
		padding-bottom: 32px;
		display: flex;
		flex-direction: column;
		gap: 12px;

		li:last-child {
			@media screen and (min-width: 992px) {
				margin-right: 20px;
			}
		}

		@media screen and (min-width: 992px) {
			padding: 0;
			flex-direction: row;
			gap: 100px;
			border: none;
		}

		@media screen and (min-width: 1100px) {
			gap: 120px;
		}

		@media screen and (min-width: 1300px) {
			gap: 170px;
		}

		&-head {
			font-weight: 600;
			font-size: 14px;
			line-height: 150%;
			text-transform: capitalize;
			color: #ffffff;
			background-image: url(../img/arrow.svg);
			background-repeat: no-repeat;
			background-position: center right;

			@media screen and (min-width: 992px) {
				background: none;
				margin-bottom: 15px;
			}
		}

		&-links {
			@media screen and (min-width: 992px) {
				display: flex;
				flex-direction: column;
			}
		}

		&-link {
			font-size: 14px;
			line-height: 170%;
			text-transform: capitalize;
			color: #a4a4a4;
			transition: all 0.3s ease-in-out;

			&:hover,
			&:active {
				color: #fff;
			}
		}

		&-contact {
			font-size: 14px;
			line-height: 170%;
			text-transform: capitalize;
			color: #a4a4a4;
			transition: all 0.3s ease-in-out;
		}
	}

	&-links {
		margin: 34px 0 19px;
		display: flex;
		justify-content: center;
		align-items: center;
		gap: 20px;

		@media screen and (min-width: 992px) {
			margin: 7px 0 42px;
			justify-content: flex-start;
			gap: 15px;
		}

