		flex-wrap: nowrap;
		justify-content: space-between;
	}

	&-card-black {
		background-color: #252525;
		padding: 48px 46px 94px 27px;
		flex-direction: column;
		margin: 0 20px 30px;

		@media screen and (min-width: 992px) {
			min-width: 329px;
			flex-basis: 23.1%;
			margin: 0;
		}

		.rooms-card-head {
			margin-top: 14px;
			margin-bottom: 24px;
