			position: relative;

			&:before {
				content: "";
				display: block;
				position: absolute;
				top: 0;
				bottom: 0;
				left: 0;
				right: 0;
				width: 100%;
				height: 100%;
				background: linear-gradient(
					360deg,
					rgba(0, 0, 0, 0.65) -16.21%,
					rgba(196, 196, 196, 0) 118.83%
				);
			}
		}

		span {
