			line-height: 25px;
			color: #ffffff;
		}

		&-parag {
			font-size: 18px;
			line-height: 28px;
			color: #bcbcbc;
		}

		&-info {
			position: absolute;
			bottom: 20px;
			left: 28px;
		}
	}
