.banner {
	width: 100%;
	margin: 0 auto 100px;
	position: relative;
	overflow: hidden;
	filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.08));
	background: linear-gradient(
			120deg,
			rgba(255, 255, 255, 0.7) 100%,
			transparent 12%
		),
		url(../img/banner-bg.jpg);
	background-repeat: no-repeat;
	background-position: top right;

	@media screen and (min-width: 924px) {
		background: linear-gradient(
				120deg,
				rgba(255, 255, 255, 0.9) 63.7%,
				transparent 12%
			),
			url(../img/banner-bg.jpg);
		background-repeat: no-repeat;
		background-position: top right;
	}

	@media screen and (min-width: 1300px) {
		background: linear-gradient(
				120deg,
				rgba(255, 255, 255, 0.9) 43.7%,
				transparent 12%
			),
			url(../img/banner-bg.jpg);
		background-repeat: no-repeat;
		background-position: top right;
	}

	&-text {
		padding: 25px 22px;
		max-width: 532px;
		text-align: center;
		z-index: 2;
		position: relative;

		@media screen and (min-width: 520px) {
			padding: 39px 32px;
			text-align: start;
		}

		h2 {
			font-size: 30px;
			line-height: 35px;
			color: #000000;
			margin-bottom: 10px;

			@media screen and (min-width: 520px) {
				font-size: 40px;
				line-height: 60px;
			}
		}

		p {
			font-size: 16px;
			line-height: 20px;
			color: #616161;

			@media screen and (min-width: 520px) {
				font-size: 20px;
				line-height: 28px;
			}
		}
	}

	&-left {
		@media screen and (min-width: 1300px) {
			right: 52.7%;
		}

		@media screen and (min-width: 924px) {
			background: #fff;
		}

		width: 100%;
		height: 115.4%;
		position: absolute;
		right: 30%;
		top: 0;
		z-index: 0;
		transform: rotate(30deg) skew(0deg, -30deg);
		transform-origin: top right;
		filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.08));
	}
}

